.card {
  height: 200px;
  background: #6CADDC;
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  overflow: hidden;
 
  text-align: center;
}

img { 
  background: no-repeat center center fixed;
  background-size: cover;
  height: 100%;
}

 




